___CSS_LOADER_EXPORT___.push([module.id, `.tabs-wr {
  margin-top: 32px;
}
.tabs__items {
  display: flex;
  margin-bottom: 16px;
}
.tabs__videos {
  display: none;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 24px;
}
.tabs__videos.active {
  display: grid;
}
.tabs__videos .creator__video {
  width: auto;
}
.tabs__videos .creator__video:last-of-type {
  margin-right: 0;
}
.tabs__bio {
  display: none;
}
.tabs__bio.active {
  display: grid;
}
.tabs__playlists {
  display: none;
}
.tabs__playlists.active {
  display: block;
}
.tabs .playlist__videos {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 24px;
}
.tabs .playlist__videos.active {
  display: grid;
}
.tabs .playlist__videos .creator__video {
  width: auto;
}
.tabs .playlist__videos .creator__video:last-of-type {
  margin-right: 0;
}
.tabs .creator__video {
  width: auto;
}

.tabs__bio {
  margin-top: 25px;
  gap: 24px;
  grid-template-columns: 1fr 28%;
}

.bio__title {
  color: #bbb;
  font-size: 12px;
  font-weight: 400;
  margin-bottom: 4px;
}
.bio__descr {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
}
.bio__social {
  display: flex;
  width: 100%;
  padding: 24px;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  border-radius: 8px;
  background: var(--9, #222);
}
.bio__social_title {
  font-size: 18px;
  font-weight: 600;
}
.bio__social_line {
  display: flex;
  gap: 8px;
  align-items: center;
  cursor: pointer;
}
.bio__social_text {
  color: #bbb;
  font-size: 14px;
  font-weight: 400;
}

.tab__item {
  cursor: pointer;
  color: #777;
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px; /* 150% */
  padding: 5px 24px;
}
.tab__item.active {
  color: #fff;
  border-bottom: 1px solid #ad7955;
}

@media screen and (max-width: 1440px) {
  .tabs__videos {
    grid-template-columns: 1fr 1fr;
  }
  .tabs .playlist__videos {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 996px) {
  .tabs__bio {
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width: 768px) {
  .tabs__videos {
    grid-template-columns: 1fr;
  }
  .tabs .playlist__videos {
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width: 576px) {
  .tabs-wr {
    margin-top: 200px;
  }
