___CSS_LOADER_EXPORT___.push([module.id, `.banner {
  background: grey;
  width: 100%;
  height: 480px;
  display: grid;
  margin-top: 24px;
  border-radius: 15px;
  grid-template-columns: 1fr 41%;
  background-repeat: no-repeat;
  background-size: cover;
}
.banner__left {
  padding-top: 133px;
}
.banner__center {
  display: flex;
  margin-top: 33px;
  padding-left: 3px;
}
.banner_avatar-wr {
  display: flex;
  align-items: center;
}
.banner__data {
  display: flex;
  margin-right: 29px;
}
.banner__inner-data-wr {
  margin-left: 18px;
  margin-top: 7px;
  display: block;
}
.banner__data-num {
  font-size: 18px;
  font-weight: 700;
}
.banner__data-text {
  font-size: 12px;
  font-weight: 300;
}
.banner__subs-btn {
  margin-top: 32px;
}
.banner_avatar_img {
  width: 64px;
  border-radius: 64px;
  height: 64px;
  display: flex;
  align-items: center;
  margin-right: 8px;
}
.banner_avatar_text {
  font-size: 32px;
  font-weight: 600;
}

@media screen and (max-width: 996px) {
  .banner_avatar-wr {
    display: block;
  }
  .banner {
    grid-template-columns: 1fr 1fr;
    justify-content: center;
  }
}
@media screen and (max-width: 576px) {
  .banner_avatar-wr {
    display: block;
  }
  .banner {
    grid-template-columns: 1fr;
    justify-content: center;
    height: 155.709px;
  }
  .banner_avatar_img {
    display: block;
    margin: 0 auto;
  }
  .banner_avatar_text {
    font-size: 24px;
    font-weight: 600;
    margin-top: 10px;
  }
  .banner__empty {
    display: none;
  }
  .banner__left {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 120px;
  }
  .banner__center {
    justify-content: space-around;
    display: flex;
    padding: 0;
    border-radius: 8px;
    background: var(--9, #222);
    width: 100%;
    margin-top: 15px;
    width: 100%;
  }
  .banner__data {
    width: fit-content;
    margin: 0;
  }
  .banner .button {
    margin-top: 15px;
  }
